<template>
  <div
    v-show="scrollTop >= visibleHeight"
    class="fx-back-top"
    :style="styles"
    @click="onClick"
  >
    <slot>
      <icon icon="UpCircleOutlined" />
    </slot>
  </div>
</template>

<script>
import Icon from '../Icon'
import { addScrollEvent, removeScrollEvent } from '../helpers/events'
import { getScrollDom } from '../helpers/dom'

export default {
  name: 'fx-back-top',
  components: { Icon },
  props: {
    visibleHeight: {
      type: Number,
      default: 200
    },
    // 是否需要动画
    animated: {
      type: Boolean,
      default: true
    },
    // 偏移量，格式为 [x, y]
    offset: {
      type: Array,
      default() {
        return [0, 0]
      }
    }
  },
  data() {
    return {
      scrollTop: 0,
      duration: 200
    }
  },
  computed: {
    styles() {
      return {
        transform: `translate3d(${this.offset[0]}px, ${this.offset[1]}px, 0px)`
      }
    }
  },
  created() {
    this.scrollTop = getScrollDom().scrollTop

    addScrollEvent(this.onScroll)
  },
  beforeDestroy() {
    removeScrollEvent(this.onScroll)
  },
  methods: {
    onScroll(e) {
      this.scrollTop = e.target.documentElement.scrollTop
    },

    onClick(e) {
      this.toTop()

      this.$emit(e.type, e)
    },

    toTop() {
      getScrollDom().scrollTo({
        top: 0,
        behavior: this.animated ? 'smooth' : 'instant'
      })
    }
  }
}
</script>
